.side-bar {
  position: fixed;
  top: size(41px);
  right: size(56px);
  height: 100%;
  display: flex;
  flex-direction: column;
  padding-bottom: size(33px);
  transition: opacity $trans-default;

  @include vp-1023 {
    opacity: 0;
  }

  &__wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: size(10px);
    z-index: 50;

    @include vp-1023 {
      position: fixed;
      top: 0;
      right: 12%;
      height: 100%;
      width: 50%;
      background-color: $color-white-smoke;
      overflow: auto;
      padding-top: size(52px);
      transition: transform $trans-default;
      transform: translate3d(100%, 0, 0);
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    @include vp-1023 {
      padding: 0 size(30px) 0 size(15px);
      margin-bottom: size(50px);
      align-self: flex-end;

      &--order {
        order: 2;
      }
    }
  }

  &__item {
    display: block;
    max-width: size(80px);

    &-text {
      display: none;
      max-width: size(200px);
      font-family: $ff-pragmatica;
      font-style: normal;
      font-weight: 300;
      font-size: size(14px);
      line-height: size(20px);
      letter-spacing: 0.07em;
      text-transform: uppercase;
      color: $color-default-black;
    }
  }

  &__item-link {
    display: inline;
    max-width: size(80px);
    word-wrap: break-word;
    white-space: pre-wrap;

    @include vp-1023 {
      font-size: size(14px);
      line-height: size(24px);
    }
  }

  &__item--support {
    margin: size(30px) 0 size(20px);
  }
}

.side-bar--opened {
  @include vp-1023 {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    padding-top: size(40px);
    z-index: 1;
    overflow: auto;
    opacity: 1;
  }
}

.side-bar__wrapper--opened {
  @include vp-1023 {
    transition: transform $trans-default;
    transform: translate3d(24%, 0, 0);
  }

  .side-bar__list {
    position: relative;
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    padding-right: size(12px);
    padding-left: size(12px);
    margin-bottom: size(31px);
  }

  .side-bar__item {
    max-width: 100%;
  }

  .side-bar__item a {
    font-size: size(12px);
    line-height: size(21px);
  }

  .side-bar__item-text {
    display: block;
    font-size: size(12px);
    margin-bottom: size(15px);
  }

  .side-bar__list--opened {
    display: grid;

    .side-bar__item--support {
      grid-row: 2/3;
      margin: 0 0 size(10px);
    }
  }
}
