.footer {
  padding: size(20px) 0 size(7px) 0;
  background-color: $color-white-smoke;

  @include vp-767 {
    padding-bottom: 0;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 33.6% 34% 33%;

    @media (min-width: 2100px) {
      grid-template-columns: 33.6% 22% 33%;
    }


    @include vp-1439 {
      display: flex;
      justify-content: space-between;
    }

    @include vp-767 {
      display: grid;
      grid-template-columns: 1fr;
    }
  }

  &__item {
    max-width: size(276px);
    margin-bottom: size(10px);

    @include vp-767 {
      margin-top: size(20px);
      margin-bottom: size(5px);
    }
  }

  &__item a {
    display: inline-block;
    text-transform: lowercase;
    font-size: size(12px);
    letter-spacing: 0;

    &:hover {
      opacity: 0.7;
    }

    @include vp-767 {
      font-size: size(13px);
      letter-spacing: 0.07rem;
    }
  }

  &__item-title {
    text-transform: uppercase;
    font-weight: 300;
    font-size: size(12px);
    line-height: size(20px);
    letter-spacing: 0.07em;
    margin: 0;
    margin-bottom: size(6px);

    @include vp-767 {
      font-size: size(14px);
      margin-bottom: size(3px);
    }
  }

  &__item-link.link {
    color: $color-pink-swan;
    display: inline-flex;

    &:hover {
      opacity: 0.7;
    }

    @include vp-767 {
      text-transform: uppercase;
      font-size: size(12px);
      letter-spacing: 0.07rem;
    }
  }

  &__item-telegram.link {
    display: block;
    width: size(120px);
    text-transform: uppercase;
    font-weight: 300;
    font-size: size(12px);
    line-height: size(20px);
    letter-spacing: 0.07em;
    margin: 0;
    margin-bottom: size(20px);

    @include vp-767 {
      margin-bottom: size(10px);
    }
  }

  &__item-text {
    margin: size(-1px);

    @include vp-767 {
      font-size: size(14px);
      letter-spacing: 0.02em;
      line-height: size(20px);
    }
  }

  &__item--policy {
    @include vp-767 {
      display: flex;
      max-width: 100%;
      flex-direction: row;
      justify-content: space-between;
      order: 3;
      margin-top: size(42px);
    }
  }

  &__item--address {
    @include vp-767 {
      order: 2;
    }
  }
}
