.link {
  font-family: $ff-pragmatica;
  font-style: normal;
  font-weight: 300;
  font-size: size(12px);
  line-height: size(20px);
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: $color-default-black;
  transition: opacity $trans-default;

  &.is-active {
    color: $color-default-black;
    pointer-events: none;
  }

  &--nobel {
    color: $color-nobel;
  }
}
