.social {
  margin-top: size(116px);

  @include vp-767 {
    margin-top: size(59px);
  }

  &__text {
    letter-spacing: 0.07em;
    line-height: size(20px);
    text-transform: uppercase;
    max-width: size(930px);

    @include vp-767 {
      font-weight: 300;
      font-size: size(14px);
      line-height: size(20px);
      letter-spacing: 0.02em;
    }

    p {
      margin: 0 0 size(20px);

      @include vp-767 {
        margin: 0 0 size(8px);
      }
    }
  }

  &__list {
    margin-top: size(35px);
    margin-bottom: 0;
    padding: 0;
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
    gap: size(16px);

    @media (max-width: 1072px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: size(8px);
    }

    @include vp-1023 {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: size(8px);
    }

    @include vp-767 {
      margin-top: size(12px);
      margin-bottom: size(105px);
    }
  }

  &__item {
    list-style: none;
  }

  &__list img {
    width: size(416px);
    height: size(416px);
    object-fit: cover;

    @include vp-1023 {
      justify-content: space-between;
      width: 100%;
      height: auto;
      margin: 0;
    }
  }
}
