.card-list {
  flex-grow: 1;

  & > .card-list__list {
    list-style: none;
    padding: 0;
    margin: 0;

    @include vp-767 {
      margin-bottom: size(18px);
    }
  }

  &__item {
    position: relative;
    width: 100%;
    padding: 0;
    margin: 0 0 size(9px) 0;
    border-bottom: size(1px) solid rgba($color-pink-swan, 0.2);

    @include vp-767 {
      border-bottom: none;
      margin-bottom: size(17px);

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__total {
    font-size: size(24px);
    line-height: size(32px);
    text-align: right;
    letter-spacing: 0.05em;
    text-transform: uppercase;

    @include vp-767 {
      font-size: size(12px);
      line-height: size(20px);
      letter-spacing: 0.07em;
    }

    span {
      margin-right: size(6px);

      &:first-child {
        display: none;

        @include vp-767 {
          display: inline-block;
        }
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__close-button {
    position: absolute;
    top: 0;
    right: size(-47px);
    background-color: $color-transparent;
    padding: size(11px) size(20px);

    svg {
      width: size(12px);
      height: size(12px);
    }

    @include vp-1279 {
      top: size(18px);
      transform: translateY(-50%);
    }

    @include vp-1023 {
      right: size(-4px);
    }

    @include vp-767 {
      top: size(44px);
    }

    @include vp-374 {
      top: size(24px);
    }
  }
}
