.text-content {
  .policy__block {
    display: flex;
    margin-bottom: size(22px);

    @include vp-767 {
      flex-wrap: wrap;
      margin-bottom: size(40px);
    }
  }

  ol,
  ul {
    margin: 0 0 size(10px);
    padding: 0 0 0 size(19px);
  }

  h2 {
    font-weight: 300;
    font-size: size(14px);
    line-height: size(18px);
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }

  p {
    font-weight: 300;
    font-size: size(14px);
    line-height: size(18px);
    margin: 0 0 size(8px);
  }

  li {
    font-weight: 300;
    font-size: size(14px);
    line-height: size(18px);
    padding-left: size(2px);
  }


  a {
    color: $color-default-black;
    text-decoration: none;
    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.7;
    }
  }
}
