.main {
  &-card {
    display: grid;
    grid-template-columns: 33.8% 1fr;
    grid-template-rows: minmax(size(456px), min-content);


    @media (max-width: 1438px) {
      grid-template-rows: min-content;
    }

    @include vp-1023 {
      grid-template-columns: 1fr;
    }
  }

  &-card:hover {
    opacity: 1;
  }

  &-card__video video {
    width: size(848px);
    height: size(455px);
    object-fit: fill;

    @media (max-width: 1438px) {
      width: 100%;
      height: auto;
    }
  }

  &-card__text {
    padding-top: size(7px);
    max-width: size(280px);
    font-weight: 300;
    font-size: size(14px);
    line-height: size(18px);
    letter-spacing: 0.07rem;

    @include vp-767 {
      max-width: size(320px);
    }
  }

  &-card__category {
    margin: 0;
    text-transform: uppercase;
    margin-bottom: size(2px);
    font-weight: 300;
    font-size: size(12px);
    line-height: size(20px);
    letter-spacing: 0.07rem;

    @include vp-767 {
      margin: 0;
      text-transform: uppercase;
      margin-bottom: size(2px);
      font-weight: 300;
      font-size: size(14px);
      line-height: size(20px);
      letter-spacing: 0.07rem;
    }
  }

  &-card__description {
    margin: 0;
    font-weight: 300;
    font-size: size(14px);
    line-height: size(18px);
    letter-spacing: 0;


    @include vp-1023 {
      margin-bottom: size(20px);
    }

    @include vp-767 {
      letter-spacing: 0;
      font-size: size(14px);
      line-height: size(18px);
      margin-bottom: size(23px);
    }
  }
}
