.custom-toggle {
  position: relative;

  display: flex;
  width: size(500px);
  padding: size(20px) 0;

  &__error {
    position: absolute;
    bottom: 0;
    left: 0;

    color: $color-red;

    opacity: 0;

    transition: opacity $trans-default;
  }

  input {
    position: absolute;

    width: size(1px);
    height: size(1px);
    margin: size(-1px);

    clip: rect(0 0 0 0);
  }

  label {
    position: relative;

    padding-left: size(24px);

    cursor: pointer;

    font-weight: 300;
    font-size: size(12px);
    line-height: size(20px);

    span {
      margin-bottom: 0;
      margin-left: 0;
    }
  }

  &--checkbox {
    .custom-toggle__icon {
      position: absolute;
      top: size(2px);
      left: 0;

      width: size(16px);
      height: size(16px);

      border: size(1px) solid rgba($color-default-black, 0.2);

      transition: border $trans-default;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;

        width: size(6px);
        height: size(6px);

        background-color: $color-transparent;

        transform: translate(-50%, -50%);

        transition: background-color $trans-default;
      }
    }
  }

  &--radio {
    .custom-toggle__icon {
      position: absolute;
      top: size(2px);
      left: 0;

      width: size(16px);
      height: size(16px);

      border: size(1px) solid $color-default-black;
      border-radius: 50%;

      transition: border $trans-default;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;

        width: size(8px);
        height: size(8px);

        color: $color-transparent;

        border-radius: 50%;
        transform: translate(-50%, -50%);

        transition: color $trans-default;
      }
    }
  }

  input:checked + .custom-toggle__icon {
    border: size(1px) solid $color-default-black;

    &::before {
      background-color: $color-default-black;
    }
  }

  &.is-invalid {
    .custom-toggle__icon {
      border-bottom: size(1px) solid $color-red;
    }

    .custom-toggle__error {
      opacity: 1;
    }
  }

  /*&.is-valid {
    label span {
      color: $color-eucalyptus;
    }
  }*/
}
