.form {
  margin: size(37px) 0 size(32px);

  &__fieldset {
    border: none;
    padding: 0;
    margin: size(4px) 0 size(23px);

    @include vp-767 {
      margin-bottom: size(17px);
    }

    & legend {
      padding: 0;
      margin: 0;

      @include vp-767 {
        margin-bottom: size(4px);
      }
    }

    &--radio {
      display: flex;
    }

    .custom-toggle--radio {
      width: min-content;
      padding: size(7px) size(24px) size(7px) 0;

      &:last-child {
        padding-right: 0;
      }
    }
  }

  .custom-textarea {
    margin-top: size(-5px);
    padding-bottom: size(20px);

    span {
      font-weight: 400;
      font-size: size(12px);
      line-height: size(20px);
      letter-spacing: 0.07em;
      text-transform: uppercase;

      @include vp-767 {
        text-transform: lowercase;
        font-size: size(12px);
        line-height: size(20px);
        letter-spacing: initial;
      }
    }

    &:last-child {
      padding: 0 0 size(10px) 0;
    }

    label {
      font-weight: 400;
      font-size: size(14px);
      line-height: size(24px);
      letter-spacing: 0.05em;
    }

    textarea {
      width: 100%;
      margin-top: size(13px);
      padding: size(10px);
      min-height: size(120px);
    }
  }

  .btn[type="submit"] {
    width: 100%;
    background-color: $color-default-black;
    color: $color-default-white;
    justify-content: center;
    font-weight: 300;
    font-size: size(12px);
    line-height: size(20px);
    letter-spacing: 0.07em;
    text-transform: uppercase;
    padding: size(12px) size(20px);
    margin: 0;
  }

  @include vp-1023 {
    margin: size(29px) 0 size(32px);

    .custom-textarea textarea,
    .btn[type="submit"] {
      width: 100%;
    }
  }
}
