.slider {
  @include vp-767 {
    height: size(300px);
  }
}

.slider__slider {
  height: 86%;
  width: 100%;

  @include vp-1023 {
    height: 100%;
  }

  .swiper-slide {
    transform: translate3d(0,0,0);
    backface-visibility: hidden;
    overflow: hidden;
    display: flex;
    justify-content: center;

    @include vp-1279 {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    @include vp-767 {
      align-items: flex-end;

      img {
        height: 80%;
      }
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    color: $color-default-black;
    transition: color $trans-default;
    user-select: none;

    &:after,
    &:before {
      content: none;
    }

    svg {
      fill: none;
      width: size(12px);
      height: size(16px);
    }

    &:hover {
      color: rgba($color-default-black, 0.5);
    }

    @include vp-767 {
      top: 56%;
    }
  }

  .swiper-button-prev {
    left: size(32px);
    right: auto;

    @include vp-767 {
      left: 0;
    }
  }

  .swiper-button-next {
    left: auto;
    right: size(32px);

    @include vp-767 {
      right: 0;
    }
  }

  img {
    max-width: unset;
    object-fit: cover;
    object-position: top;
    user-select: none;

    @include vp-1023 {
      max-width: 100%;
    }

    @include vp-767 {
      height: 100%;
      width: auto;
    }
  }
}

.slider__thumb {
  width: 70%;
  height: 20%;
  box-sizing: border-box;
  padding: size(10px) 0;

  @include vp-1023 {
    display: none;
  }

  .swiper-slide {
    box-sizing: border-box;
    width: size(92px);
    height: size(64px);
    border: size(1px) solid $color-transparent;
    cursor: pointer;
    transition: opacity $trans-default;
    overflow: hidden;
    display: flex;
    justify-content: center;

    @include vp-1279 {
      display: flex;
      align-items: center;
    }

    img {
      user-select: none;
      height: 100%;
      width: auto;

      @include vp-1919 {
        height: auto;
        width: 100%;
      }
    }

    &:hover {
      opacity: 0.6;
    }
  }

  .swiper-slide-thumb-active {
    border: size(1px) solid $color-very-light-grey;
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: right top;

    @include vp-1279 {
      height: auto;
      object-position: right bottom;
    }
  }
}
