.btn {
  display: inline-flex;
  margin: size(10px) 0;
  padding: size(10px) size(20px);
  font-weight: 300;

  color: $color-default-black;

  background-color: $color-white-smoke;
  border: none;
  cursor: pointer;
  user-select: none;

  transition: opacity $trans-default;

  &--red {
    background-color: $color-red;
  }


  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @media (hover: hover),
  screen and (min-width: 0\0) {

    &:hover,
    &:focus {
      opacity: 0.7;
    }
  }

  &--close {
    color: $color-very-light-grey;
    transition: color $trans-default;

    @include vp-767 {
      padding: size(5px);
    }

    @media (hover: hover),
    screen and (min-width: 0\0) {

      &:hover,
      &:focus {
        opacity: 1;
        color: $color-default-black;
        transition: color $trans-default;
      }
    }
  }
}
