.main {
  padding: size(230px) 0 size(30px);

  @include vp-767 {
    padding: size(181px) 0 size(30px);
  }

  &__list {
    margin: 0;
    padding: 0;
  }

  &__list-item {
    margin: 0;
    padding: 0;
    list-style: none;
    margin-bottom: size(10px);

    @media (max-width: 1438px) {
      margin-bottom: size(10px);
    }

    @include vp-1023 {
      margin-bottom: size(50px);
    }

    @include vp-767 {
      margin-bottom: size(73px);
    }

    &:last-child {
      .main__item-description {
        min-height: size(10px);
        margin-bottom: size(18px);
      }
    }
  }
}
