// используйте для скрытия нежелательных анимаций-скачков при поворотах экрана
@keyframes disableBreakpointAnimation {
  0% {
    visibility: hidden;
    opacity: 0;
  }

  100% {
    visibility: hidden;
    opacity: 0;
  }
}

@keyframes marquee {
  0% {
    transform: translateX(110%);
  }

  100% {
    transform: translateX(-30%);
  }
}
