.header {
  padding: size(10px) 0;
  display: flex;
  align-items: center;
  background-color: $color-white-smoke;
  z-index: 1;

  &__overlay {
    background-color: rgba(0, 0, 0, -0.5);
  }

  &__overlay::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
  }

  @include vp-1023 {
    position: fixed;
    width: 100%;
    z-index: 2;
  }

  .container {
    display: flex;
    align-items: center;
  }

  &__logo {
    position: fixed;
    top: size(12px);
    left: size(398px);
    display: inline-block;
    width: size(40px);
    height: size(11px);
    z-index: 9999;

    &--visible {
      display: none;
    }

    @include vp-1023 {
      margin-top: size(-2px);
    }

    @media (max-width: 1430px) {
      position: static;
    }
  }

  &__logo img {
    color: $color-default-black;
    width: size(34px);
    height: size(11px);
    object-fit: contain;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__inner-link {
    max-width: size(120px);

    @include vp-1023 {
      display: none;
    }
  }

  &__inner-link--visible {
    @include vp-1023 {
      display: block;
    }
  }

  &__wrapper {
    width: 100%;
    display: grid;
    grid-template-columns: size(421px) 1fr;

    @include vp-1023 {
      grid-template-columns: 1fr;
    }
  }

  &__marquee {
    position: relative;
    overflow-x: hidden;
  }

  &__text {
    margin: 0;
    line-height: size(12px);
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: $color-pink-swan;
    width: 100%;
    padding-left: size(15px);
    padding-top: size(4px);
    animation: marquee 20s linear infinite;

    @include vp-1023 {
      display: none;
    }
  }

  .link {
    max-width: size(220px);
  }
}


.header__cart {
  margin-bottom: size(15px);
  position: fixed;
  top: size(9px);
  right: size(45px);
  cursor: pointer;

  &-counter {
    padding-left: size(16px);

    @include vp-767 {
      margin-left: size(-10px);
    }
  }

  @include vp-1023 {
    position: absolute;
    top: size(11px);
    left: 50%;
    right: unset;
    transform: translate(-50%);
    margin-bottom: 0;
  }
}
