.custom-input,
.custom-textarea {
  position: relative;

  display: flex;
  width: 100%;
  padding: 0 0 size(24px) 0;

  label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;

    span {
      z-index: 1;
      font-weight: 300;
      font-size: size(12px);
      line-height: size(20px);
    }

    input,
    textarea {
      flex-grow: 1;
      font-size: size(16px);
      line-height: size(28px);
      padding: 0 size(5px) size(2px) 0;
      width: 100%;

      outline: none;
      background-color: $color-white-smoke;

      transition: border $trans-default;
    }

    input {
      border: none;
      border-bottom: size(1px) solid $color-default-black;
      height: size(32px);
      font-size: size(16px);

      &:hover {
        border-bottom: size(1px) solid rgba($color-default-black, 0.5);
      }

      &:focus {
        border-bottom: size(1px) solid rgba($color-default-black, 0.8);
        font-size: size(16px);
      }

      &:-webkit-autofill {
        background-color: inherit;
        box-shadow: inset 0 0 0 size(1000px) $color-white-smoke;
      }
    }

    textarea {
      border: size(1px) solid $color-default-black;
      padding: size(10px);
      margin-top: size(10px);
      font-size: size(16px);

      &:hover {
        border: size(1px) solid rgba($color-default-black, 0.5);
      }

      &:focus {
        border: size(1px) solid rgba($color-default-black, 0.8);
        font-size: size(16px);
      }
    }
  }

  &__error {
    position: absolute;
    right: 0;
    bottom: size(5px);

    color: $color-red;

    opacity: 0;

    transition: opacity $trans-default;
  }

  &.is-invalid {
    input {
      border-bottom: size(1px) solid $color-red;

      &:focus,
      &:hover {
        border-bottom: size(1px) solid rgba($color-red, 0.5);
      }
    }

    .custom-input__error {
      opacity: 1;
    }
  }
}
