.category {
  padding-top: size(110px);
  margin: 0 auto size(108px);

  @include vp-767 {
    margin: 0 auto size(20px);
  }

  @media (min-width: 1441px) {
    margin: 0;
  }

  @include vp-767 {
    padding-top: size(70px);
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: size(77px) size(16px);
    margin-bottom: size(27px);

    @include vp-1023 {
      grid-template-columns: repeat(3, 1fr);
      gap: size(57px) size(16px);
    }

    @include vp-767 {
      grid-template-columns: repeat(2, 1fr);
      margin-bottom: size(148px);
      gap: size(39px) size(16px);
    }

    @media (max-width: 474px) {
      grid-template-columns: 1fr;
      margin-bottom: size(171px);
    }
  }
}
