.section__wrapper {
  display: block;

  &--2-col {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;

    &>* {
      width: calc((100% / 2) - #{size(116px)});
      flex-shrink: 0;
    }

    @include vp-1023 {
      flex-wrap: wrap;

      &>* {
        width: 100%;
        flex-shrink: 0;
      }
    }
  }

  &--3-col {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;

    &>* {
      width: calc((100% / 3) - #{size(16px)});
      flex-shrink: 0;
    }
  }
}

.section--cart {
  .section__wrapper {
    position: relative;
    margin-top: size(155px);
    margin-bottom: size(155px);
    height: 100%;
    min-height: size(300px);

    @include vp-1023 {
      margin-top: size(55px);
    }

    @include vp-767 {
      margin-top: size(63px);
      margin-bottom: size(55px);
    }
  }
}
