.product {
  &__item {
    margin-top: size(64px);
    margin-bottom: size(70px);

    @include vp-1023 {
      margin-top: size(68px);
      margin-bottom: size(128px);
    }

    @include vp-767 {
      margin-top: size(64px);
    }
  }

  &__recommend {
    margin-bottom: size(202px);

    @include vp-1023 {
      margin-bottom: size(129px);
    }

    @include vp-767 {
      margin-bottom: size(87px);
    }

    .section__title {
      font-weight: 300;
      font-size: size(12px);
      line-height: size(20px);
      letter-spacing: 0.07em;
      text-transform: uppercase;
      margin: 0 0 size(10px) 0;

      @include vp-1023 {
        font-size: size(24px);
        line-height: size(32px);
        letter-spacing: 0.05em;
      }
    }
  }
}
