.card {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  align-content: stretch;
  justify-content: space-between;
  max-width: 100%;
  min-height: 100%;

  @include vp-767 {
    &:last-child {
      padding-bottom: 0;
    }
  }

  .card__block {
    margin-bottom: size(20px);

    @include vp-767 {
      margin-bottom: size(10px);
    }

    .card__image {
      width: 100%;
      height: size(279px);
      margin-bottom: size(20px);
      display: flex;
      align-items: center;
      justify-content: center;

      img {
        width: 100%;
        max-width: 100%;
        height: auto;
        object-fit: cover;
        user-select: none;
        pointer-events: none;
      }

      @include vp-1023 {
        max-height: size(230px);
        margin-bottom: size(8px);

        img {
          width: unset;
        }
      }

      @include vp-767 {
        width: calc(100% + #{size(16px)});
        max-height: unset;
        height: size(364px);
        margin-bottom: 0;
        margin-left: size(-8px);
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;

        img {
          width: auto;
          min-height: 100%;
          object-fit: cover;
        }
      }

      @include vp-374 {
        img {
          width: 100%;
          height: auto;
        }
      }
    }

    .card__info {
      width: 100%;
      max-width: initial;
      padding: 0;

      @include vp-1023 {
        padding: 0;
      }

      .card__title {
        font-size: size(12px);
        line-height: size(20px);
        letter-spacing: 0.07em;
        text-transform: uppercase;
        margin-bottom: size(7px);

        @include vp-1023 {
          font-size: size(14px);
          line-height: size(24px);
          letter-spacing: 0.05em;
          margin: 0;
        }
      }

      .card__description {
        font-weight: 300;
        font-size: size(12px);
        line-height: size(16px);
        letter-spacing: unset;
        max-width: 85%;

        p {
          margin: 0;
        }

        @include vp-1023 {
          font-size: size(14px);
          line-height: size(20px);
          letter-spacing: 0.02em;
          margin: 0;
          max-width: unset;
        }
      }
    }
  }

  .card__inner {
    display: flex;
    flex-direction: row;
    align-items: center;

    .card__price {
      font-size: size(12px);
      line-height: size(20px);
      text-align: left;
      letter-spacing: 0.07em;
      text-transform: uppercase;
      padding: 0;

      @include vp-767 {
        margin: size(10px) 0 0 size(8px);
        font-size: size(14px);
        line-height: size(24px);
        letter-spacing: 0.05em;
        justify-self: start;
      }

      .card__currency {
        margin-left: size(5px);
      }
    }

    .card__button {
      .btn {
        font-size: size(12px);
        font-weight: 300;
        line-height: size(20px);
        letter-spacing: 0.07em;
        text-transform: uppercase;
        padding: 0;
        margin: 0 size(23px) 0 0;
      }

      @include vp-767 {
        padding: size(11px) 0 0 0;

        .btn {
          padding: 0;
          font-size: size(14px);
          font-weight: 300;
          line-height: size(24px);
          letter-spacing: 0.05em;
        }
      }
    }
  }
}

.card--cart {
  flex-direction: row;

  .card__block {
    margin-bottom: 0;
    width: size(210px);
    max-height: size(174px);
    flex-shrink: 0;

    @include vp-767 {
      width: size(176px);
      max-height: size(136px);
      margin-bottom: 0;
      padding-right: size(12px);
      flex-shrink: 0;
    }

    @include vp-374 {
      width: size(120px);
    }

    .card__image {
      height: 100%;
      margin-bottom: 0;

      img {
        width: auto;
        height: 100%;
        object-fit: cover;
        user-select: none;
        pointer-events: none;
      }

      @include vp-1279 {
        img {
          width: 100%;
          height: auto;
        }
      }

      @include vp-1023 {
        width: 80%;
      }

      @include vp-767 {
        width: 100%;
        height: 100%;
        margin: 0;

        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  .card__inner {
    padding: size(16px) 0 0 size(7px);
    align-items: flex-start;
    flex-grow: 1;

    @include vp-1279 {
      padding: size(16px) 0 size(16px) size(7px);
      flex-wrap: wrap;
    }

    @include vp-1023 {
      flex-grow: 1;
      justify-content: space-around;
      flex-wrap: nowrap;
      padding-right: size(60px);
    }

    @include vp-767 {
      flex-grow: 0;
      padding: size(44px) 0 size(16px) size(7px);
      flex-wrap: wrap;
      justify-content: flex-start;
      align-content: flex-start;
    }

    @include vp-374 {
      padding: size(24px) 0 size(16px) size(3px);
    }

    .card__info {
      max-width: 55%;

      @include vp-1279 {
        max-width: 100%;
        width: 100%;
        margin-bottom: size(6px);
      }

      @include vp-1023 {
        width: unset;
      }

      @include vp-767 {
        width: 100%;
        margin-bottom: size(5px);
        padding-right: size(30px);
      }

      .card__title {
        margin: 0;
        transition: opacity $trans-600;
        opacity: inherit;
        display: inline-block;

        @include vp-767 {
          font-weight: 300;
          font-size: size(12px);
          line-height: size(20px);
          letter-spacing: 0.07em;
          text-transform: uppercase;
        }
      }

      .card__description {
        font-weight: 300;
        font-size: size(14px);
        line-height: size(18px);
        padding-bottom: size(10px);

        p {
          margin: 0;
        }

        @include vp-1279 {
          max-width: 80%;
        }

        @include vp-767 {
          display: none;
          font-size: size(14px);
          line-height: size(20px);
          letter-spacing: 0.02em;
        }
      }
    }

    .card__count {
      display: flex;
      align-items: center;
      width: size(50px);
      padding: 0;
      margin-left: auto;
      align-self: start;
      flex-shrink: 0;

      @include vp-1279 {
        text-align: left;
        padding: 0;
        margin-left: unset;
      }

      @include vp-767 {
        margin-top: 0;
        margin-left: size(-5px);
      }

      .custom-input {
        padding: 0;

        input {
          width: size(20px);
          font-weight: 300;
          font-size: size(16px);
          line-height: size(16px);
          height: size(24px);
          text-align: center;
          letter-spacing: 0.05em;
          text-transform: uppercase;
          border: none;
          padding: 0;

          &:hover,
          &:focus {
            border: none;
            font-size: size(16px);
          }

          @include vp-767 {
            width: size(10px);
            font-size: size(16px);
            line-height: size(16px);
            height: size(20px);

            &:focus {
              font-size: size(16px);
            }
          }
        }
      }

      .btn {
        flex-shrink: 0;
        padding: 0 size(5px);
        font-weight: 300;
        font-size: size(14px);
        line-height: size(24px);
        text-align: center;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        margin: 0;
        background-color: $color-transparent;

        &:disabled {
          opacity: 0.4;
        }

        @include vp-767 {
          line-height: size(14px);
          padding: 0 size(5px);
        }

        &--minus {
          padding-bottom: size(2px);

          @include vp-767 {
            padding-bottom: size(2px);
          }
        }
      }
    }

    .card__price {
      font-weight: 300;
      font-size: size(14px);
      line-height: size(24px);
      text-align: right;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      width: size(90px);
      padding: 0;
      flex-shrink: 0;

      @include vp-767 {
        font-size: size(12px);
        line-height: size(17px);
        letter-spacing: 0.07em;
        padding: 0;
        margin: 0;
        text-align: center;
      }
    }
  }
}

.card--product {
  min-height: size(536px);
  flex-direction: row;
  align-items: center;

  @media (min-width: size(1441px)) {
    justify-content: flex-start;
  }

  @include vp-1023 {
    flex-wrap: wrap;
  }

  @include vp-767 {
    align-content: flex-start;
    min-height: size(470px);
  }

  .card__block {
    height: size(536px);
    max-width: 58%;
    z-index: 0;

    @include vp-1023 {
      max-width: 100%;
    }

    @include vp-767 {
      max-width: unset;
      width: calc(100% + #{size(16px)});
      height: size(370px);
      margin-left: size(-8px);
      margin-bottom: size(6px);

      .swiper-slide {
        padding-bottom: size(15px);
        padding-right: size(7px);

        img {
          height: 85%;
        }
      }
    }

    .card__slider {
      height: 100%;
    }
  }

  .card__inner {
    flex-wrap: wrap;
    padding-left: size(14px);
    margin-top: size(-60px);

    @include vp-1023 {
      margin-top: 0;
      padding-left: 0;
    }

    .card__info {
      width: 100%;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      justify-content: flex-end;
      margin-bottom: size(48px);

      @include vp-1023 {
        padding: 0;
        margin-bottom: size(20px);
      }

      @include vp-767 {
        margin-bottom: 0;
      }

      .card__title {
        margin: 0 0 size(9px) 0;
        font-size: size(24px);
        line-height: size(32px);
        letter-spacing: 0.05em;

        @include vp-1023 {
          font-size: size(24px);
          line-height: size(32px);
          letter-spacing: 0.05em;
          text-transform: uppercase;
        }
      }

      .card__description {
        font-weight: 300;
        font-size: size(14px);
        line-height: size(20px);
        letter-spacing: 0.02em;
        margin: 0;
        max-width: 75%;

        p {
          margin: 0;
        }

        @include vp-1279 {
          max-width: 85%;
        }

        @include vp-1023 {
          max-width: 90%;
          font-weight: 300;
          font-size: size(12px);
          line-height: size(20px);
          letter-spacing: 0.07em;
          text-transform: uppercase;
        }
      }
    }

    .card__button {
      padding: 0;
      width: min-content;
      flex-shrink: 0;

      .btn {
        text-transform: uppercase;
        font-weight: 300;
        font-size: size(24px);
        line-height: size(32px);
        letter-spacing: 0.05em;
        text-align: left;
        margin: 0;
        padding: 0;
      }

      @include vp-1023 {
        padding: 0;
        margin-top: size(6px);

        .btn {
          padding: 0;
          font-size: size(14px);
          line-height: size(24px);
          font-weight: 300;
          letter-spacing: 0.05em;
          text-transform: uppercase;
        }
      }
    }

    .card__price {
      width: auto;
      font-size: size(24px);
      line-height: size(32px);
      letter-spacing: 0.05em;
      text-transform: uppercase;
      text-align: left;
      margin-left: size(40px);
      flex-shrink: 0;

      @include vp-1023 {
        margin: size(6px) 0 0 size(77px);
        text-align: left;
        font-size: size(14px);
        line-height: size(24px);
        letter-spacing: 0.05em;
        text-transform: uppercase;
      }

      @include vp-767 {
        margin: size(17px) 0 0 size(77px);
      }
    }
  }
}

.card--mini {

  .card__block {
    margin-bottom: size(15px);

    .card__image {
      height: size(249px);
      margin-bottom: 0;

      img {
        width: unset;
      }
    }

    .card__info {
      .card__description {
        margin-top: size(7px);
      }
    }

    @include vp-767 {
      margin-bottom: 0;

      .card__image {
        width: 100%;
        height: size(192px);
        margin-bottom: size(13px);
      }
    }
  }

  .card__inner {
    @include vp-767 {
      .card__price {
        margin: size(10px) 0 0 size(1px);
      }
    }
  }
}

.category__card {
  max-width: size(416px);

  @include vp-767 {
    .card__block {
      .card__image {
        height: size(378px);
        margin-left: size(-7px);
      }

      .card__info {
        margin-top: size(-13px);
      }
    }

    .card__inner {
      .card__price {
        margin: size(10px) 0 0 size(1px);
      }
    }
  }
}
