.nav-toggle {
  display: none;
  position: relative;
  left: size(-2px);
  width: size(40px);
  height: size(20px);
  padding: 0;
  border: none;
  cursor: pointer;
  z-index: 2;
  background-color: transparent;
  font-family: $ff-pragmatica;
  font-style: normal;
  font-weight: 300;
  font-size: size(12px);
  line-height: size(20px);
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: $color-default-black;

  @include vp-1023 {
    display: block;
  }

  &--opened {
    left: size(-4px);
    font-size: 0;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: size(14px);
      height: size(1px);
      background-color: transparent;
    }

    &::before {
      left: size(20px);
      top: size(10px);
      transform: rotate(45deg);
      background-color: $color-very-light-grey;
    }

    &::after {
      left: size(20px);
      top: size(10px);
      transform: rotate(-45deg);
      background-color: $color-very-light-grey;
    }
  }
}
