.order-block {
  padding: size(16px) 0;


  &__info {
    letter-spacing: 0.07em;
    text-transform: uppercase;
    line-height: size(20px);

    p {
      margin-top: 0;
      margin-bottom: size(20px);

      &:last-child {
        margin-bottom: 0;
      }
    }

    & span {
      display: block;
    }

    a {
      margin: 0 size(3px);
      transition: opacity $trans-600;

      &:hover {
        opacity: 0.5;
        transition: opacity $trans-600;
      }

      &:focus {
        opacity: 0.8;
        transition: opacity $trans-600;
      }
    }

    @include vp-767 {
      padding-right: size(20px);
    }
  }
}
