.recommendation {
  z-index: 0;

  .swiper-wrapper {
    list-style: none;
    margin: 0;
    padding: 0;

    .swiper-slide {
      height: unset;
    }
  }

  @media (min-width: 1441px) {
    justify-content: flex-start;
  }

  @include vp-1023 {
    width: calc(100% + #{size(32px)});
    padding-left: size(16px);
    padding-right: size(16px);
    margin-left: size(-16px);
  }

  @include vp-767 {
    width: calc(100% + #{size(16px)});
    padding-left: size(8px);
    padding-right: size(8px);
    margin-left: size(-8px);
  }
}
