.policy {
  padding: size(171px) 0 size(77px);
  counter-reset: h2;

  @include vp-767 {
    padding: size(173px) 0 size(29px);
  }

  .policy__block {
    display: flex;
    margin-bottom: size(22px);

    @include vp-767 {
      flex-wrap: wrap;
      margin-bottom: size(40px);
    }

    ol,
    ul {
      margin: 0 0 size(10px);
      padding: 0 0 0 size(19px);
    }
  }

  .policy__content-wrapper {
    max-width: size(631px);

    @include vp-767 {
      max-width: unset;
      padding-right: size(3px);
    }
  }

  h2 {
    width: size(308px);
    margin: 0 size(124px) 0 0;
    flex-shrink: 0;

    @include vp-1023 {
      margin: 0 size(24px) 0 0;
      width: size(250px);
    }

    @include vp-767 {
      width: 96%;
      margin: 0 0 size(22px);
      padding: 0;
    }

    &::before {
      counter-increment: h2;
      content: counter(h2) ".";
      margin-right: size(5px);
    }
  }

  .policy__text {
    font-weight: 300;
    font-size: size(14px);
    line-height: size(18px);
    margin: 0 0 size(8px);
  }

  .policy__list-item {
    font-weight: 300;
    font-size: size(14px);
    line-height: size(18px);
    padding-left: size(2px);
  }
}
